import { StaticImage } from 'gatsby-plugin-image';
import React, { useEffect, useState } from 'react';
import { Bounce, Flip } from 'react-reveal';

const imageSize = '10rem';
const borderRadius = 128;
const styles = {
  avatar: {
    flexShrink: 1,
    borderRadius: borderRadius,
    WebkitBorderRadius: borderRadius,
    display: 'inline-block',
    border: '8px solid rgba(33,104,105, 0.5)',
    maxHeight: imageSize,
    minHeight: imageSize,
    maxWidth: imageSize,
    minWidth: imageSize,
  },
  text: {
    paddingLeft: '1em',
    display: 'flex',
    justifyContent: 'center',
    flexDirection: 'column',
  },
};

export default function SplashScreen({ avatar, entries }) {
  const [tech, setTech] = useState(getRandomElement(entries));

  useEffect(() => {
    const interval = setInterval(() => {
      let randomElem = getRandomElement(entries);
      while (randomElem === tech) {
        randomElem = getRandomElement(entries);
      }
      setTech(getRandomElement(entries));
    }, 2000);
    return () => clearInterval(interval);
  });

  const renderTextHighlighted = ({ label, logo }) => {
    return (
      <Flip bottom key={label}>
        <div style={{ display: 'inline-block' }}>
          {logo}
          <span className="has-text-primary"> {label}</span>
        </div>
      </Flip>
    );
  };

  return (
    <Bounce left>
      <div className="container is-flex">
        <StaticImage
          aspectRatio={1}
          height={imageSize}
          width={imageSize}
          style={styles.avatar}
          imgStyle={{ borderRadius }}
          __imageData={avatar}
        />
        <div style={styles.text}>
          <p className="title is-size-1">
            Hi! I'm <span className="has-text-primary">Kevin</span>
          </p>
          <p className="subtitle is-size-4">
            the freelance Web-Developer ready for your next{' '}
            {renderTextHighlighted(tech)} Project.
          </p>
        </div>
      </div>
    </Bounce>
  );
}

function getRandomElement(list) {
  return list[Math.floor(Math.random() * list.length)];
}
