import { graphql } from 'gatsby';
import React from 'react';
import { Helmet } from 'react-helmet';
import AboutMe from '../components/AboutMe';
import Contact from '../components/Contact';
import Experience from '../components/cv/Experience';
import Skills from '../components/cv/Skills';
import Info from '../components/head/Info';
import Footer from '../components/nav/Footer';
import References from '../components/References';
import '../scss/styles.scss';

const IndexPage = ({ data }) => {
  const { contentfulPortfolio } = data;
  const title = contentfulPortfolio.title ?? 'Kevelopment';

  const lang = 'en';
  const description =
    'Gatsby Portfolio Website for Kevin Adam - Freelance Software Developer';

  console.log({ contentfulPortfolio });
  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>{title}</title>
        <html lang={lang} />
        <meta name="description" content={description} />
      </Helmet>
      <Info
        avatar={contentfulPortfolio.logo.gatsbyImageData}
        logos={contentfulPortfolio.logos}
        technologies={contentfulPortfolio.technologies}
      />
      <AboutMe
        profileImage={contentfulPortfolio.profile.gatsbyImageData}
        description={contentfulPortfolio.aboutMe.raw}
      />
      <Experience />
      <Skills skills={contentfulPortfolio.skills} />
      <References />
      <Contact description={contentfulPortfolio.contact.raw} />
      <Footer />
    </>
  );
};

export default IndexPage;

export const query = graphql`
  query {
    contentfulPortfolio {
      title
      aboutMe {
        raw
      }
      contact {
        raw
      }
      skills {
        qualities
        techStack {
          languages
          frameworks
          tools
          misc
        }
      }
      logo {
        gatsbyImageData
      }
      profile {
        gatsbyImageData
      }
      logos {
        gatsbyImageData
        title
      }
    }
  }
`;
