import { documentToReactComponents } from '@contentful/rich-text-react-renderer';
import { StaticImage } from 'gatsby-plugin-image';
import React from 'react';
import { Fade } from 'react-reveal';
import { Element } from 'react-scroll';
import Routes from './nav/Routes';

export default function AboutMe({ profileImage, description }) {
  return (
    <Element name={Routes.ABOUTME}>
      <section className="hero is-light is-fullheight gradient-primary-background py-6">
        <div className="hero-body is-primary">
          <div className="container has-text-centered">
            <p className="title has-text-light my-6">About Me</p>
            <div>
              <div className="content is-flex is-justify-content-center px-6">
                <Fade left>
                  <div className="is-flex-grow-2">
                    <StaticImage __imageData={profileImage} alt="Me" />
                  </div>
                </Fade>
                <Fade right>
                  <div
                    className="container is-flex-grow-3 px-6"
                    style={{ maxWidth: '60vw' }}
                  >
                    <div className="has-text-white has-text-left is-size-4">
                      {documentToReactComponents(JSON.parse(description))}
                    </div>
                  </div>
                </Fade>
              </div>
            </div>
          </div>
        </div>
      </section>
    </Element>
  );
}
