import Navbar from '../nav/Navbar';
import Routes from '../nav/Routes';
import SplashScreen from './SplashScreen';

const paths = [
  {
    path:
      'M -0.206 0.121 C -118.052 395.178 -1089.42 1365.007 -67.875 1001.29 C 791.724 695.233 597.014 1193.514 1520.272 1001.81 C 1991.772 903.908 1839.789 38.547 1922.586 0',
    opacity: 0.5,
  },
  {
    path:
      'M -0.233 0.121 C -118.079 395.178 -1040.209 1300.593 -32.989 898.907 C 543.966 668.814 678.732 1161.694 1436.885 936 C 1836.3 817.098 1833.689 54.624 1931.812 -0.658',
    opacity: 0.5,
  },
  {
    path:
      'M -0.206 0.121 C -118.052 395.178 -1053.987 1164.926 -13.361 860.066 C 858.123 604.758 733.72 1222.427 1488.839 960.703 C 1850.565 835.329 1889.497 8.837 2027.918 -13.793',
    opacity: 0.5,
  },
  {
    path:
      'M -0.168 0.121 C -118.014 395.178 -1027.245 1161.255 4.902 828.819 C 871.188 549.804 844.467 1160.446 1453.573 875.254 C 1841.586 693.581 1810.369 69.758 1921.15 2.248',
    opacity: 1,
  },
];

export default function Info({ avatar, logos, technologies }) {
  const entries = generateTechnologies(logos, technologies);
  console.log({ avatar });
  return (
    <Element name={Routes.INFO}>
      <section className="hero is-fullheight is-light gradient-primary-background">
        <BackgroundSvg elements={paths} />
        <div className="hero-head">
          <Navbar />
        </div>

        <div className="hero-body absolutely-centered">
          <SplashScreen avatar={avatar} entries={entries} />
        </div>
      </section>
    </Element>
  );
}

const logoSize = 32;
const styles = {
  logo: {
    verticalAlign: 'middle',
    width: logoSize,
    height: logoSize,
    marginBottom: 2,
  },
};

function generateTechnologies(logos) {
  console.log({ logos });
  return logos.map(({ title, gatsbyImageData }) => ({
    label: title,
    logo: (
      <StaticImage
        width={logoSize}
        height={logoSize}
        aspectRatio={1}
        style={styles.logo}
        alt={title + ' Logo'}
        __imageData={gatsbyImageData}
      />
    ),
  }));
}
